	module.exports = {"data":{"wordpressPost":{"title":"LJ &#038; RYAN 30/11/2017","content":"<h1 class=\"text-align-center\">Lj and Ryan</h1>\n<h3 class=\"text-align-center\">30TH NOVEMBER 2017 / LA MON HOTEL &amp; COUNTRY HOUSE</h3>\n<h3 class=\"text-align-center\">GOLD &amp; GLAM WEDDING STATIONERY</h3>\n<p><img class=\"alignnone wp-image-63 size-full\" src=\"http://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJR.jpg\" alt=\"\" width=\"750\" height=\"669\" srcset=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJR.jpg 750w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJR-300x268.jpg 300w\" sizes=\"(max-width: 706px) 89vw, (max-width: 767px) 82vw, 740px\" /></p>\n<p>The lovely Lesley-Jayne and Ryan wanted to keep things simple and sentimental and I was honoured to be a help them have the big day of their dreams!</p>\n<p><img class=\"alignnone wp-image-64 size-full\" src=\"http://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/1-2.jpeg\" alt=\"\" width=\"750\" height=\"422\" srcset=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/1-2.jpeg 750w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/1-2-300x169.jpeg 300w\" sizes=\"(max-width: 706px) 89vw, (max-width: 767px) 82vw, 740px\" /></p>\n<p>Their invitations were kraft paper accented with gold, and I chose to continue this theme with their On the Day stationery too. Having created poem cards for their invitations, I used the same script font throughout, keeping the design classic and simple. LJ&#8217;s Grandfather had built an easel (from scratch!!) to house a vintage mirror. I then created small cards for their seating plan, with some gold glitter for added glitz!</p>\n<p><img class=\"alignnone wp-image-69 size-large\" src=\"http://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171124_1202060-576x1024.jpg\" alt=\"\" width=\"525\" height=\"933\" srcset=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171124_1202060-576x1024.jpg 576w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171124_1202060-169x300.jpg 169w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171124_1202060-768x1365.jpg 768w\" sizes=\"(max-width: 525px) 100vw, 525px\" /></p>\n<p>With their table numbers, they wanted &#8220;When we were this age&#8221; theme, and the photos were just SO CUTE! Festive and fun, I am so happy with how everything turned out and hope that they had the best day.</p>\n<p>I wish you a life full of laughs and love together as Mr &amp; Mrs Gregg!</p>\n<h3>LOVE, LOUISE XX</h3>\n<p>&nbsp;</p>\n<div id='gallery-1' class='gallery galleryid-62 gallery-columns-2 gallery-size-medium'><figure class='gallery-item'>\n\t\t\t<div class='gallery-icon portrait'>\n\t\t\t\t<img width=\"168\" height=\"300\" src=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/SEATINGPLANLJ-168x300.jpg\" class=\"attachment-medium size-medium\" alt=\"\" srcset=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/SEATINGPLANLJ-168x300.jpg 168w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/SEATINGPLANLJ-573x1024.jpg 573w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/SEATINGPLANLJ.jpg 742w\" sizes=\"100vw\" />\n\t\t\t</div></figure><figure class='gallery-item'>\n\t\t\t<div class='gallery-icon landscape'>\n\t\t\t\t<img width=\"300\" height=\"296\" src=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171130_140557-300x296.jpg\" class=\"attachment-medium size-medium\" alt=\"\" srcset=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171130_140557-300x296.jpg 300w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171130_140557-768x759.jpg 768w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171130_140557-1024x1011.jpg 1024w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/20171130_140557-100x100.jpg 100w\" sizes=\"100vw\" />\n\t\t\t</div></figure><figure class='gallery-item'>\n\t\t\t<div class='gallery-icon landscape'>\n\t\t\t\t<img width=\"300\" height=\"300\" src=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJ-300x300.jpg\" class=\"attachment-medium size-medium\" alt=\"\" srcset=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJ-300x300.jpg 300w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJ-150x150.jpg 150w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJ-768x768.jpg 768w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJ-1024x1024.jpg 1024w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/LJ-100x100.jpg 100w\" sizes=\"100vw\" />\n\t\t\t</div></figure><figure class='gallery-item'>\n\t\t\t<div class='gallery-icon portrait'>\n\t\t\t\t<img width=\"226\" height=\"300\" src=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/015-226x300.jpg\" class=\"attachment-medium size-medium\" alt=\"\" srcset=\"https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/015-226x300.jpg 226w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/015-768x1021.jpg 768w, https://www.cwc.liffydesigns.co.uk/wp-content/uploads/2018/03/015-771x1024.jpg 771w\" sizes=\"100vw\" />\n\t\t\t</div></figure>\n\t\t</div>\n\n<h3 class=\"text-align-center\">CREDITS : TOP PHOTOGRAPH &#8211; LUCY BELL</h3>\n<h3 class=\"text-align-center\">STATIONERY PHOTOGRAPHS :<strong> <a href=\"https://www.creativeweddingcompany.com/\">CWC</a> </strong>| VENUE : <strong><a href=\"https://www.lamon.co.uk/\">LA MON HOTEL</a></strong></h3>\n<p>&nbsp;</p>\n"}},"pathContext":{"id":"9435049d-00b9-5220-b081-ba85e0cd908e"}}

